.about-section .container {
    display: flex;
}

.about-content {
    padding: 84px 80px 84px 30px;

    background-color: $accent-color;
}

.about-subtitle {
    margin-bottom: 5px;

    font-weight: 400;
    font-size: 20px;
    line-height: 1.65;
}

.about-title {
    margin-bottom: 25px;

    font-weight: 300;
    font-size: 38px;
    line-height: 1.26;
}

.about-text {
    margin-bottom: 20px;
}

.about-button {
    @extend %btn;

    background-color: $accent-color;
    color: $primary-white-color;
    border: 1px solid #fff;

}